import Ajax from 'core/ajax';
import Log from 'core/log';
import {getDataAndUpdate} from './renderer';
export default class BoardInterface {
    /**
     *
     *
     * @param {Board} boardState
     */
    constructor(boardState) {
        this.boardState = boardState;
    }

    /**
     * Send change action
     *
     * @param {string} action
     * @param {array} data
     */
    sendChange(action, data) {
        const promises = Ajax.call(
            [
                {
                    methodname: 'mod_kanban_change_kanban_content_' + action,
                    args: {
                        'cmid': this.boardState.cmid,
                        'boardid': this.boardState.boardid,
                        'data': data
                    }
                }
            ]
        );

        promises[0].then(data => {
            getDataAndUpdate(this.boardState);
            return data;
        }).catch((error) => {
            Log.error(error);
            return false;
        });
    }
}